import ArticleList from '../../../components/articleList.vue'
export default {
	components: {
		ArticleList
	},
	data() {
		return {
			title: '党建动态',
			list: [
				{
					time: '2019-06-01',
					title: '习近平出席“不忘初心、牢记使命”主题教育工作会 议并发表重要讲话。',
					abstract: '无',
					publish: '管理员发布',
					lookNum: 7789
				},
				{
					time: '2019-06-01',
					title: '习近平出席“不忘初心、牢记使命”主题教育工作会 议并发表重要讲话。',
					abstract: '无',
					publish: '管理员发布',
					lookNum: 7789
				}
			]
		}
	},
	created() {
		this.getData()
	},
	methods: {
		getData() {
			var data = {
				category: "工会党建信息发布", 
				channel: "党建动态"
			}
			this.$axios(this.$api.getList , data , 'post')
			.then(res =>{
				console.log(res, '.then--------');
				if(res.error_code == 0){
					this.list = res.data.data
				}
			})
			.catch(res =>{
				console.log(res,'.catch--------')
			})
		},
	}
}



